import React from 'react';
// import logo from './logo.svg';
import './App.css';

import Main from "./components/main";
import Seachbar from "./components/searchBar";
import Sidebar from "./components/sidebar"
import {Container,Row,Col} from "react-bootstrap";

import axios from "axios";





export default class App extends React.Component{



  constructor(){


    super()



    this.handleChange=this.handleChange.bind(this)
  }



  handleChange(video){



    this.setState({

      selectedItem:video
    })
    console.log(video)

  }





  state={

    items:[],
    selectedItem:""


  }










 getSearchParam(value){
   axios.get('https://www.googleapis.com/youtube/v3/search',
   {

    params:{

      part:"snippet",
      maxResults:10,
      key:'AIzaSyBzFBD7FJnsDvBqAnU3gfsbsOBvfr7UYsI',
      q:value
    }
   })

   .then((response)=>{

    let {items}=response.data
    console.log(items)
    this.setState({
      items:items,

      selectedItem:items[0]
    })

    console.log(this.state.selectedItem.id.videoId)
   })
   .catch((err)=>console.log(err.response))

 }
 // getSearchParam=getSearchParam.bind(this)

  componentDidMount(){

    axios.get('https://www.googleapis.com/youtube/v3/search',{

      params:{
        part:"snippet",
        maxResults:10,

        key:'AIzaSyBzFBD7FJnsDvBqAnU3gfsbsOBvfr7UYsI',

        q:"hurricane vlog"
      }
    })
    .then((response)=>{

      let {items}=response.data
      this.setState({

        items:items,
        selectedItem:items[0]
      })
    })
    .catch((err)=>console.log(err.response))




  }

  render(){


    return(

      <>


      <Row  className="mt-3 ml-3">

    <Col md={8}>

      <Seachbar  getSearchParam={this.getSearchParam.bind(this)}/>

    </Col>
{/*
    <Col md={4}>

   

     <h1>hello world</h1>
      

    </Col>*/}

        </Row>

      


     
        

     
      <Row  md={12}  style={{height:"100vh"}}>
      <Col md={8} className="test-a">
      <Main   video={this.state.selectedItem}/>
        
      </Col>

      <Col md={4}  className="test-b">

          <Sidebar videos={this.state.items} handleChange={this.handleChange}/>

      </Col>
        

      </Row>


      

     

  

      </>
      )
  }
}

