import React from "react";

import ItemList from "./itemList";


export default  class Sidebar extends React.Component{


	constructor(props){

		super()
	}



	render(){

		const item=this.props.videos.map((video)=>{


			return (

				<ItemList src={video.snippet.thumbnails.medium} video={video} key={video.id.videoId}  handleChange={this.props.handleChange}/>



				// <div key={video.snippet.channelId}>

				// <img src="video.snippet.thumbnails.medium" alt="" style={{width:"100%"}}/>
				// </div>

				)
		})

		if (this.props.videos!=null  || undefined) {




			console.log(this.props.videos)


			// console.log(this.props.videos[0].snippet.thumbnails.medium)



		}




		


		return (

			<>

			{item}

			</>

			)
	}
}